.main-header {
				border-bottom: 1px solid #d4dde6;
}

@media (max-width: 600px) {
				.main-header {
								display: none;
				}
}

.main-header__logo {
				font-size: 37px;
				font-size: 2.3125rem;
				font-size: calc(37 / 16 * 1rem);
				min-width: 143.73px;
				min-width: 8.98313rem;
				min-width: calc((127.73 + 16) / 16 * 1rem);
				display: block;
				text-decoration: none;
				color: #7a9a01;
}

.main-header__logo:hover, .main-header__logo:focus {
				color: #7a9a01;
				text-decoration: inherit;
}

@media (min-width: 960px) {
				.main-header__logo ~ .main-header__slogan {
								margin-left: 24px;
								margin-left: calc((1920px - 100vw) * -0.025 + 24px);
				}
}

@media (min-width: 1920px) {
				.main-header__logo ~ .main-header__slogan {
								margin-left: 24px;
				}
}

.main-header__limit {
				max-width: 1920px;
				margin: 0 auto;
				padding: 0 16px;
}

.main-header__row:not(:empty) {
				margin: -4px -8px;
}

.main-header__align {
				display: flex;
				flex-wrap: wrap;
				align-items: center;
				justify-content: center;
}

.main-header__align > *:not(script) {
				padding: 0 8px;
				margin: 4px 0;
}

.main-header__padding {
				padding: 16px 0;
}

.main-header__brand-row:not(:empty) {
				margin: -4px -8px;
}

.main-header__brand-align {
				display: flex;
				flex-wrap: wrap;
				align-items: center;
				justify-content: center;
}

.main-header__brand-align > *:not(script) {
				padding: 0 8px;
				margin: 4px 0;
}

.main-header__slogan {
				text-align: center;
				font-size: 13px;
}

.main-header__phone {
				display: inline-block;
				line-height: 1;
				font-weight: 500;
				color: #2f4b55;
				font-size: 0.875rem;
}

.main-header__box ~ .main-header__box:not(:empty) {
				margin-top: 8px;
}

@media (max-width: 480px) {
				.main-header__retail {
								display: none;
				}
}

.main-header__retail-row:not(:empty) {
				margin: -4px -8px;
}

.main-header__retail-align {
				display: flex;
				flex-wrap: wrap;
				justify-content: center;
				align-items: center;
}

.main-header__retail-align > *:not(script) {
				padding: 0 8px;
				margin: 4px 0;
}

.main-header__address-row:not(:empty) {
				margin: -2px -8px;
}

.main-header__address-align {
				display: flex;
				flex-wrap: wrap;
				justify-content: center;
				align-items: center;
}

.main-header__address-align > *:not(script) {
				padding: 0 8px;
				margin: 2px 0;
}

.main-header__address-slog {
				font-size: 12px;
				font-size: 13.71429px/remrem;
				color: #718391;
}

.main-header__address-slog ~ .main-header__address-slog {
				position: relative;
}

.main-header__address-slog ~ .main-header__address-slog:before {
				content: '|';
				position: absolute;
				right: 100%;
				width: 1px;
				margin-right: .5px;
}

@media (min-width: 960px) {
				.main-header__align {
								justify-content: space-between;
				}
}
